import React from 'react';
import "bootstrap/dist/css/bootstrap.min.css";

const Navbar = () => {
    return(
<nav className="navbar navbar-expand-lg navbar-light bg-light">
                <div className="container-fluid">
                  <a className="navbar-brand" href={"#"}>DELL</a>
                  <button className="navbar-toggler" type={"button"} data-bs-toggle={"collapse"} data-bs-target={"#navbarScroll"} aria-controls={"navbarScroll"} aria-expanded={"false"} aria-label={"Toggle navigation"}>
                    <span className="navbar-toggler-icon"></span>
                  </button>
                  <div className="collapse navbar-collapse" id={"navbarScroll"}>
                    <ul className="navbar-nav me-auto my-2 my-lg-0 navbar-nav-scroll" style={{"--bs-scroll-height": "100px;"}}>
                      <li className="nav-item">
                        <a className="nav-link active" aria-current="page" href={"#"}>Home</a>
                      </li>
                      <li className="nav-item dropdown">
                        <a className="nav-link dropdown-toggle" href={"#"} id={"navbarScrollingDropdown"} role={"button"} data-bs-toggle={"dropdown"} aria-expanded={"false"}>
                          About Me
                        </a>
                        <ul className="dropdown-menu" aria-labelledby={"navbarScrollingDropdown"}>
                          <li><a className="dropdown-item" href={"https://www.linkedin.com/in/fadhilla-rayes/"} target={"blank"}>LinkedIn</a></li>
                          <li><a className="dropdown-item" href={"https://github.com/fadhillarayes"} target={"blank"}>GitHub</a></li>
                          
                        </ul>
                      </li>
                    </ul>
                    <form className="d-flex">
                      <input className="form-control me-2" type={"search"} placeholder={"Search"} aria-label={"Search"}/>
                      <button className="btn btn-outline-success" type={"submit"}>Search</button>
                    </form>
                  </div>
                </div>
          </nav>
    )};

    export default Navbar;